
<template>
  <div  :style="{width:MobileSet.DivWidth,'min-width':MobileSet.DivMinWidth,'padding-left':MobileSet.paddingLeft}">

    <img src="../../static/img/del1.png" style="z-index:0;width: 18px;height: 18px;position:absolute;left: 95px;top: 20px;"
         :style="{'left':MobileSet.LeftImg}"  @click="ClearSearchText()"  v-show="searchText.length>0 || ShowClose " />
    <input
      id="searchInput"
      type="text"
      list="sg-nav-search"
      v-model.trim="searchText"
      class="form-control fa fa-search"
      placeholder="Search"
      clearable
      style="min-width:60px;max-width:360px;float:left;padding-right:-30px;"
      :style="{width:MobileSet.searchTextWidth}"
      @keydown.enter="KeyDownEnterSearch($event)"
      @change="SetSearchTextSaveFile()"
      @keydown="SetSearchText()"
      @focus="PopSelect()"

    />



    <!-- 用来遮住原生向下箭头 -->

<!--    <div class="sg-down-arrow"   v-if="searchList.length"  style="height: 28px;width: 28px;position:absolute;left: 410px;top: 20px;"  > </div>-->

    <datalist id="sg-nav-search"  >
      <option
        v-for="(item,$index) in searchList"
        :key="$index"
        :value="item">
      </option>
    </datalist>

  </div>

</template>
<script>
  export default {
    name: "SearchInput",
    data() {

      let searchText=localStorage.getItem('searchText');
      if (searchText==null)
        searchText='';

      return {

        MobileSet:{
          DivMinWidth:"160px",
          DivWidth:"360px",
          searchTextWidth:"115px",
          paddingLeft:"10px",
          LeftImg:"95px"
        },
        ShowClose:false,
        searchText: searchText,//搜索框内容  @select="selectSearch()"
        localStorageName: "sgNavSearchList",//保存到storage的键名
        searchList: [] //搜索记录下拉框
      };
    },
    created() {
      let widthFull=window.screen.width;
      let widthUsed= 80+45 +30;
      if (widthFull>700)
        widthUsed=widthUsed+390;

      let widthRemain=widthFull-widthUsed;

      if (!this.$parent._isMobile()){
        this.MobileSet.searchTextWidth="380px";
        this.MobileSet.DivMinWidth="160px";
        this.MobileSet.DivWidth="380px";
        this.MobileSet.paddingLeft="10px";
        this.MobileSet.LeftImg="390px";
      }else{

        this.MobileSet.searchTextWidth=widthRemain+"px";
        this.MobileSet.DivMinWidth=widthRemain+"px";
        this.MobileSet.DivWidth=widthRemain+"px";
        this.MobileSet.paddingLeft="0px";
        this.MobileSet.LeftImg="85px";
      }

      this.searchList = this.getSearchList();


    },
    mounted(){

    },
    methods: {
      SetParentSearchText(){
        this.$parent.SearchClickCallBack(this.searchText,false);
      },

      SetSearchText(){
        this.searchText= $(".form-control").val();
        this.SetParentSearchText();
      },
      ClearSearchText:function(){
        this.searchText="";
        $(".form-control").val("");
        this.ShowClose=false;
        this.SetParentSearchText();

      },
      PopSelect(){
        this.ShowClose=true;
      },
      SelectOption:function(){

        let input_select=$(".form-control").val();
        let option_length=$("option").length;
        let option_id='';
        for(let i=0;i<option_length;i++) {
          let option_value = $("option")[i].value;//.attr('data-value');
          if (input_select == option_value) {


            this.searchText=input_select;
            this.SetParentSearchText();

            $("option")[i].click();
            //this.gotoSearch();
            //option_id = $("option").eq(i).attr('data-id');
            break;
          }
        }

        },


      SetSearchTextSaveFile() {
        if (this.searchText.trim()!=""){
          //如果有数据就保存
          this.saveSearchList(this.searchText);

        }
        localStorage.setItem('searchText',this.searchText)
        this.SetParentSearchText();

      },
      KeyDownEnterSearch(e) {
        //e.preventDefault();

        if (this.searchText.trim()!=""){
          //如果有数据就保存
          this.saveSearchList(this.searchText);

        }
        localStorage.setItem('searchText',this.searchText)
        //没数据也可以回调
        this.$parent.SearchClickCallBack(this.searchText,true);

        // $(".form-control").click();
        //$("#sg-nav-search").hidden();
      },
      getSearchList() {

        // 获取搜索过的关键字
        let storageSearchList = localStorage.getItem(this.localStorageName);
        return storageSearchList ? JSON.parse(storageSearchList) : [];
      },
      saveSearchList(searchText) {
        // 存储搜索过的关键字
        let storageSearchList = localStorage.getItem(this.localStorageName);
        this.searchList = storageSearchList ? JSON.parse(storageSearchList) : [];

        this.searchList.includes(searchText) || this.searchList.push(searchText);

        if (this.searchList.length>10)
          this.searchList.shift();

        localStorage.setItem(
          this.localStorageName,
          JSON.stringify(this.searchList)
        );
      }
    }
  };
</script>





