
<style>

  /*body{background-color: red;}*/

  /*div{width: 300px;height: 200px;color: #fff;}*/

  /*div.rgba{background-color: rgba(0,0,0,0.7);}*/

  /*div.opacity{background-color: #000;filter:alpha(opacity=70);-moz-opacity:0.70;-khtml-opacity:0.7;opacity: 0.7;margin-top: 20px;}*/

  /*filter:alpha(opacity=30);-moz-opacity:0.30;-khtml-opacity:0.7;opacity: 0.7;*/
  .container-full{

    width:100%;
    height: 100%;
    position: relative;

  }

  .container-fluid{
    padding-right: 0px;
    padding-left: 0px;

    position: fixed;

    padding-top: 56px;
    height: 100%;
  }

  .footer{
    position:fixed;bottom:0;z-index:999;
  }


  .nav-left::-webkit-scrollbar {/*滚动条整体样式*/
    width: 2px;     /*高宽分别对应横竖滚动条的尺寸*/
    height: 2px;
  }
  .nav-left::-webkit-scrollbar-thumb {/*滚动条里面小方块*/
    border-radius: 2px;
    -webkit-box-shadow: inset 0 0 2px rgba(0,0,0,0.2);
    background: rgba(0,0,0,0.2);
  }
  .nav-left::-webkit-scrollbar-track {/*滚动条里面轨道*/
    -webkit-box-shadow: inset 0 0 2px rgba(0,0,0,0.2);
    border-radius: 0;
    background: rgba(0,0,0,0.1);
  }
  iframe{width:100%;height:100%;border:0}

  ul li img {width:18px;height:18px;text-align:left;margin-left: -10px;}

  div.popContainer{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 9999;
    background: rgba(0,0,0,0.3);
    display: none;
  }

  div.popMenu{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
    background: rgba(0,0,0,0.1);
    display: none;
  }
  .nav{
    margin-left: -2px;
  }

  #Search::-webkit-search-cancel-button{
    display: none;
  }

</style>

<template>

<div class="container-full">
  <nav class="navbar navbar-expand-sm bg-dark navbar-dark fixed-top" style="padding-left: 5px;padding-right: 5px;"  id="barDiv"  >
    <button type="button" class="btn btn-info " @click="LeftMenuConfig()"  data-toggle="戳这里你会很爽"
            title="戳这里你会很爽" style="padding-left: 5px;padding-right: 5px;"   >链接LP</button>


      <SearchInput ref="searchInput"  id="Search"  ></SearchInput>

    <div :class="dataParams.RightMenuClass" id="collapsibleNavbar"  style="width:300px;">
      <ul class="navbar-nav">

        <li class="nav-item" v-for="(item,index) in dataParams.CateArr "    :key="index"   >
          <a class="nav-link" :class="item.cate_index==dataParams.SiteCate? 'nav-link active ' : 'nav-link'" href="#" @click="CateClick($event,item.cate_index)" >{{item.cate_name}}</a>
        </li>


        <li class="nav-item">
          <a class="nav-link" :class="100==dataParams.SiteCate? 'nav-link active ' : 'nav-link'" href="#" @click="GardenClick()" >数字花园</a>
        </li>

        <li class="nav-item">
          <a class="nav-link" href="#" data-toggle="modal" data-target="#layer" >设置</a>
        </li>


        <li class="nav-item">
          <a class="nav-link" href="#" @click="LeftMenuConfig()">{{dataParams.LeftMenuConfigBtTxt}}</a>
        </li>
      </ul>
    </div>

    <button class="navbar-toggler" type="button"  @click="PopMenuClick()"  style="padding-left: 5px;padding-right: 5px;" >
      <span class="navbar-toggler-icon"></span>
    </button>
  </nav>

  <div class="container-fluid"   >


      <div class="nav-left" :style="{width:dataParams.LeftMenuWidth}" style="background-color:#eeeeee;
      filter:alpha(opacity=50);-moz-opacity:0.5;-khtml-opacity:0.5;opacity: 0.7;
      position:absolute;float:left;width:130px;height: 100%;padding-bottom:100px;overflow-y: scroll;overflow-x: hidden;z-index: 8999;" id="siteDiv"
           @mouseenter="NavLeftEnter()"  @mouseleave="NavLeftLeave()"      >

        <ul class="nav nav-pills flex-column "    >

        <li class="nav-item" v-for="(item,index) in dataParams.SiteArr "    :key="index"     @click="SiteClick(item)"  :data-toggle="item.site_name"
            :title="item.site_name" style="white-space:nowrap;">

          <a  class="navbar-dark" :class="item.site_index==dataParams.SiteIndex? 'nav-link disabled ' : 'nav-link'"   href="#" style="text-align: left" :data-toggle="item.site_name"
              :title="item.site_name"    >
            <img :src="item.site_ico" :alt="item.site_name"   />&nbsp;&nbsp;{{item.site_name}}</a>
        </li>

        </ul>
        <hr class="d-sm-none">
      </div>


      <div   class="nav-left" :style="{'padding-left':dataParams.ConMarginLeft}" style="z-index: 8990;-webkit-overflow-scrolling:touch;width:100%;height: 100%;padding-bottom:20px;overflow-y: scroll;"   id="engineDiv"   >
            <iframe id="engine" name="engine" v-bind:src="dataParams.SiteUrl"
            frameborder="0" marginwidth="0" marginheight="0" allowtransparency="true" style="height: 100%; width: 100%;background-color:#fff"></iframe>
    </div>

  </div>


  <nav class="bg-light fixed-bottom" style="width:100%;height: 28px;vertical-align: bottom;background-color: #eeeeee;align-content: center;color: #848B99;font:12px arial">
    <div style="padding-top:10px; ">Copyright© 2020-2050 发邮件到<a href="mailto:realsodo@hotmail.com" >realsodo@hotmail.com</a></div>

  </nav>

  <div role="dialog" class="modal fade" id="layer" >
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">

          <h4 class="modal-title">设置</h4>
          <button type="button" class="close" data-dismiss="modal">
            <span>&times;</span>
          </button>
        </div>

          <a class="nav-link" href="#" @click="SetIndex(0)">设置为启动页</a>


          <a class="nav-link" href="#" @click="ClsIndex(0)">清空启动页</a>

<!--        <div class="modal-body text-right">-->
<!--          <button class="btn btn-primary btn-sm" data-dismiss="modal">取消</button>-->
<!--          <button class="btn btn-danger btn-sm" v-on:click="deletemsg(nowIndex)" data-dismiss="modal">确认</button>-->
<!--        </div>-->
      </div>
    </div>
  </div>




  <div class='popContainer'><div style="widht:200px;height:60px;margin: auto;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;color: #eeeeee;">努力加载中，休息去喝个水吧</div></div>

  <div class='popMenu' @click="PopMenuHideClick" @scroll="PopMenuHideClick" @touchstart="PopMenuHideClick" @keydown="PopMenuHideClick" ></div>

</div>



</template>


<script>


  import SearchInput from '@/components/SearchInputEx'

  //import dropdown from '@/components/dropdown'
  //import $ from 'jquery'

  import baseData from '../assets/basedata'

  export default {
      name: "SearchComponent",
      data () {

        return {
          timer:'',
          itemlist:[],
          nodatatext:'',
          placeholder:'search',
          //LeftImg:'295px',
          dataParams: {
            LeftMenuIcoWidth:"28px",
            LeftMenuWidth:"28px",
            ConMarginLeft:"0px",
            LeftMenuConfigBtTxt:"",
            RightMenuClass:'collapse navbar-collapse',

            CateArr:baseData.cate,//分类
            SiteCate:0,
            SiteMax:3,

            SiteArr:[],
            SiteIndex:0,
            SiteUrl:'',

            SearchContent:''
          }
        }
      },
      components: {SearchInput},
      beforeDestroy() {
        clearTimeout(this.timer);
      },
      mounted(){

        // if (window !== window.top) {
        //   // 页面被嵌套在iframe中
        //   window.top.location =  self.location; // 可选：将top窗口重定向到当前页面
        //   // 或者可以显示警告信息，或者执行其他操作
        //   alert("该页面不能被嵌入iframe！");
        //   return;
        //<meta http-equiv="X-Frame-Options" content="SAMEORIGIN / DENY ">
        //<meta http-equiv="X-Frame-Options" content="ALLOW-FROM http://xxx.xxx.com">
        // }

        // window.addEventListener('message',function(event){
        //   if(event.data.refresh){
        //     //此处执行事件
        //     console.log(111);
        //   }
        // });



        //this.BindTouchEvent();
        //
        // let EngineFrame=document.querySelector('#engine');
        // let touchElement=EngineFrame.contentWindow;
        // this.BindTouchElement(touchElement);


        // let siteDiv=document.querySelector('#siteDiv');
        // this.BindTouchElement(siteDiv);
        //
        //
        let barDiv=document.querySelector('#barDiv');
        this.BindTouchElement(barDiv);



        this.loadDataInit();
        // this.NavLeftInit();

        this.HideProcess(".popContainer",1);

        window['HideProcess'] = (argDiv,argClearTime) => {
          this.HideProcess(argDiv,argClearTime);
        };


        window['addEvent'] = (elem, type, showAll) => {
          this.addEvent(elem, type, showAll);
        };

        let IframeOnClick = {
          resolution: 200,
          iframes: [],
          interval: null,
          Iframe: function() {
            this.element = arguments[0];
            this.cb = arguments[1];

            this.hasTracked = false;
          },
          track: function(element, cb) {
            this.iframes.push(new this.Iframe(element, cb));
            if (!this.interval) {
              let _this = this;
              this.interval = setInterval(function() { _this.checkClick(); }, this.resolution);
            }
          },

          checkClick: function() {


            //console.log(this.iframes[0].element.contentWindow.screenTop);


            if (document.activeElement) {
              let activeElement = document.activeElement;

              for (let i in this.iframes) {

                if (activeElement === this.iframes[i].element) { // user is in this Iframe

                  if (this.iframes[i].hasTracked == false) {
                    this.iframes[i].cb.apply(window, []);

                    this.iframes[i].hasTracked = true;
                  }
                } else {
                  this.iframes[i].hasTracked = false;
                }
              }
            }
          }
        };




        this.$nextTick(() =>{


          try{
            const iframe = document.querySelector('#engine');


            // // 监听点击事件
            // IframeOnClick.track(iframe, function() {
            //   //做你想做的事情
            //   PopMenuHideClick();
            //
            // });




            if(iframe.attachEvent){
                iframe.attachEvent("onload", function(){
                //console.log(iframe.contentWindow.document.body.innerHTML);
                //document.getElementById("iframeInner").innerHTML=iframe.contentWindow.document.body.innerHTML;
                HideProcess(".popContainer",1);

                  let touchElement = iframe.contentWindow.document.body;


                  this.BindTouchElement(touchElement);


                return;})



            }else{
                iframe.onload= function(){
                  //console.log(iframe.contentDocument.body.innerHTML);
                  //document.getElementById("iframeInner").innerHTML=iframe.contentDocument.body.innerHTML;
                  HideProcess(".popContainer",1);
                  //window.parent.postMessage({refresh:'id'},'*');
                  //EngineFrame.contentDocument.body ||




                  return;
                };


                }

              }catch(e){
                HideProcess(".popContainer",1);

                console.warn('setIframeHeight Error');
              }

          // if (iframe.attachEvent) {
          //   iframe.attachEvent("onload", function() {
          //
          //     HideProcess();
          //   });
          // } else {
          //   iframe.onload = function() {
          //     HideProcess();
          //   };
          // }
        });


      },

      methods:{
        * filterMax(arr,  maxSize) {
          if (!maxSize || maxSize > arr.length) {
            maxSize = arr.length;
          }
          let count = 0;
          let i = 0;
          while ( count< maxSize && i < arr.length ) {
            // if (condition(arr[i])) {
            yield arr[i];
            count++;
            // }
            i++;
          }
        },

        LeftMenuConfigBtTxt(){
          if (this.dataParams.LeftMenuWidth=='0px')
            this.dataParams.LeftMenuConfigBtTxt='显示导航菜单';
          else if(this.dataParams.LeftMenuWidth==this.dataParams.LeftMenuIcoWidth)
            this.dataParams.LeftMenuConfigBtTxt='加大显示导航菜单';
          else
            this.dataParams.LeftMenuConfigBtTxt='隐藏导航菜单';

        },
        LeftMenuConfig:function () {



          if (this.dataParams.LeftMenuWidth=='0px')
            this.dataParams.LeftMenuWidth=this.dataParams.LeftMenuIcoWidth;
          else if(this.dataParams.LeftMenuWidth==this.dataParams.LeftMenuIcoWidth)
            this.dataParams.LeftMenuWidth='138px';
          else
            this.dataParams.LeftMenuWidth='0px';

          this.LeftMenuConfigBtTxt();

          if (!this._isMobile()) //这个用在调整iframe的左区域大小，对于手机和电脑不一样。
          {
            this.dataParams.ConMarginLeft=this.dataParams.LeftMenuWidth;
            localStorage.setItem('LeftMenuWidth',this.dataParams.LeftMenuWidth);//调整后保存
          }

          else{
            this.dataParams.ConMarginLeft="0px";
            //localStorage.setItem('LeftMenuWidth',this.dataParams.LeftMenuIcoWidth);//调整后保存
          }



          this.PopMenuHideClick();//设置查询框的样式
        },
        CateInit(){
          //分类绑定数据,暂时没用，直接复制到初始化了，如果需要过滤还是用这个模式比较好
          this.dataParams.CateArr=baseData.cate;
        },
        CateClick:function(e,value){
          this.PopMenuHideClick();
          this.dataParams.SiteCate=value;//选择的分类赋值
          //存储分类信息
          localStorage.setItem('SiteCate',this.dataParams.SiteCate)

          this.dataParams.SiteArr=this.SiteFilterByCate(baseData.data);//加载站点



        },
        SiteFilterByCate(argBaseData){
          let ret;// 区分移动和pc网站  -1  是移动  1 是pc  0 pc移动显示
          if (this._isMobile())
            ret = argBaseData.filter(item=>item.site_show<=0 && item.site_cate==this.dataParams.SiteCate);
          else
            ret = argBaseData.filter(item=>item.site_show>=0 && item.site_cate==this.dataParams.SiteCate);

          return ret;
          //return ret.slice(0, this.dataParams.SiteMax);

          //return this.filterMax(ret,this.dataParams.siteMax);
          },
        SiteClick(siteObj){


          //可能有查询数据，可能查询数据为空
          if (this.dataParams.SiteIndex!=siteObj.site_index || this.dataParams.SiteUrl==""){
            this.dataParams.SiteIndex=siteObj.site_index;
            this.SearchContent();
            //this.NavLeftLeave();
          }else{
            this.NavLeftEnter();
          }


        },
        SearchClickCallBack(argContent,search){

          if (search){

            //给查询空间的回调方法,查询的内容可能有，也可能为空
            //if(this.dataParams.SearchContent!=argContent  ||  this.dataParams.SiteUrl==""){
              this.dataParams.SearchContent = argContent;
              this.SearchContent()
           // }
          }else{
              //都赋值
              this.dataParams.SearchContent=argContent;
          }

        },
        SearchContent(){

          let siteObj=this.dataParams.SiteArr.filter(item=>item.site_index== this.dataParams.SiteIndex && item.site_cate==this.dataParams.SiteCate )[0];

          if (siteObj!=null){//站点对象存在
            try{
              //整个查询执行没有错误就可以保存
              let _url = siteObj.site_url;
              if (this._isMobile())
                _url = siteObj.site_m;

              let tmpSearchContent=this.dataParams.SearchContent;


              if (tmpSearchContent!=null && tmpSearchContent.trim()!="")
                _url  = _url.replace(/{query_word_placeholder}/g, tmpSearchContent);
              else
                _url  = this.getBaseURL(_url);

              if (siteObj.site_mode=="1")//
                window.open(_url);
              else{
                this.ShowProcess(".popContainer",3000);
                this.dataParams.SiteUrl=_url;//绑定到iframe的url上
                this.PopMenuHideClick();


                this.SearchSave();//没有错误，嵌入的保存查询参数
              }

            }catch (e) {
              //有异常就不能保存，会陷入死循环
            }
          }
        },
        SearchSave(){
          //操作成功后
          //存储查询内容 控件自动保存了
          //localStorage.setItem('searchContent',this.dataParams.searchContent)
          //存储站点
          localStorage.setItem('SiteIndex',this.dataParams.SiteIndex)
          //存储分类信息
          localStorage.setItem('SiteCate',this.dataParams.SiteCate)

          //存储分类信息
          localStorage.setItem('searchText',this.dataParams.SearchContent)

          this.NavLeftLeave();
        },

        GardenClick:function(){

          this.dataParams.SiteCate=100;
          //window.open("garden.html");
          this.dataParams.SiteUrl="garden.html";
        },
        addEvent:function (elem, type, showAll) {
          type = type.split(' ');
          type.forEach(function(item) {
            elem.addEventListener(item, function(ev) {
              console.log(showAll ? ev : ev.type);
            });
          });
        },
        loadDataInit:function(){

          this.dataParams.SearchContent=localStorage.getItem('searchText');



          if (!this._isMobile())
          {
            let LeftMenuWidth=localStorage.getItem('LeftMenuWidth');
            if (LeftMenuWidth!=null)
              this.dataParams.LeftMenuWidth=LeftMenuWidth;

          }else{
            this.dataParams.LeftMenuWidth=this.dataParams.LeftMenuIcoWidth;
            // localStorage.setItem('LeftMenuWidth',this.dataParams.LeftMenuWidth);//调整后保存
          }



          this.LeftMenuConfigBtTxt();

          let siteCate=localStorage.getItem('SiteCate');
          if (siteCate!=null){
            this.dataParams.SiteCate=siteCate;
          }
          this.dataParams.SiteArr=this.SiteFilterByCate(baseData.data);//加载站点


        },
        BindTouchElement(touchElement){

          let startX=0,endX=0,startY=0,endY=0;
          touchElement.addEventListener('touchstart', (e) => {
            //e.preventDefault();
            setTimeout(function(){
              startX = e.changedTouches[0].clientX;
              startY=e.changedTouches[0].clientY;
            }, 5);
          }, false);
          touchElement.addEventListener('touchmove', (e) => {
            //e.preventDefault();
            endX = e.changedTouches[0].clientX;
            endY = e.changedTouches[0].clientY;
            if (endX - startX>28) this.NavLeftEnter();
            else if ( Math.abs(startX-endX)>30 && Math.abs(startY-endY)<30)this.NavLeftLeave();
          });

        },
        BindTouchEvent(){
          let siteDiv=document.querySelector('#siteDiv');
          let startX=0,endX=0,startY=0,endY=0;

          // 监听触摸开始和结束
          siteDiv.addEventListener('touchstart', (e) => {
            //e.preventDefault();
            setTimeout(function(){
              startX = e.changedTouches[0].clientX;
              startY=e.changedTouches[0].clientY;
              // 执行一些操作
            }, 5);
          }, false);



          siteDiv.addEventListener('touchend', (e) => {
            //e.preventDefault();touches[]
            //endX = e.changedTouches[0].clientX;
            endX = e.changedTouches[0].clientX;
            endY = e.changedTouches[0].clientY;

            if (endX - startX>5) { // 向右滑动
              this.NavLeftEnter();
            } else if ( startX-endX>38 && startY-endY<28) { // 向左滑动

              this.NavLeftLeave();
            }
            // else{
            //   this.NavLeftLeave();
            // }
          });



        },

        NavLeftEnter:function(){

          this.dataParams.LeftMenuWidth='138px';


          // e.focus();
          //this.PopMenuHideClick();
          //this.dataParams.navLeftBt='隐藏导航菜单';
        },
        NavLeftLeave:function(){

          this.dataParams.LeftMenuWidth= localStorage.getItem('LeftMenuWidth');

          //this.dataParams.navLeftBt='隐藏导航菜单';

        },
        PopMenuClick:function(){//查询框的处理，变大和变小

          if(this.dataParams.RightMenuClass=="collapse navbar-collapse"){
            this.dataParams.RightMenuClass="navbar-collapse collapse show";
            this.ShowProcess(".popMenu",0);
            //this.$refs.searchInput.MobileSet.LeftImg="350px";

            //this.LeftImg="150px";
            ///$('.navbar-toggler').click();
          }else{
            this.dataParams.RightMenuClass="collapse navbar-collapse";
            this.HideProcess(".popMenu",0);
            //this.$refs.searchInput.MobileSet.LeftImg="295px";
          }

        },
        PopMenuHideClick:function(){//变小查询弹出框
          this.dataParams.RightMenuClass="collapse navbar-collapse";
          this.HideProcess(".popMenu",0);
          //this.$refs.searchInput.MobileSet.LeftImg="295px";
        },
        HideProcess:function(argDiv,argTimeClear){
          $(argDiv).hide();
          if (argTimeClear>0){clearTimeout(this.timer);

          }

          //this.BindTouchIframe();

        },
        ShowProcess:function(argDiv,argTimeOut){
          $(argDiv).show();
          if (argTimeOut>0){
            this.timer=setTimeout(function () {
              this.HideProcess(argDiv,argTimeOut)
            }, argTimeOut);
          }

        }
        ,
        getBaseURL:function (url) {
          try {
            const urlObj = new URL(url);
            return `${urlObj.protocol}//${urlObj.hostname}${urlObj.port ? ':' + urlObj.port : ''}`;
          } catch (e) {
            console.error(e.message);
            return null;
          }
        },

        SetIndex:function(index){

          let value= this.dataParams.MenuArr.filter(item=>item.site_index== this.dataParams.site_index)[0];
          if (value.site_mode=="1")
          {
            alert("弹出页面不能设置成启动页面");
          }
          else{
            localStorage.setItem('setIndex', JSON.stringify(value));
          }
          this.PopMenuHideClick();


      },
        ClsIndex:function(index){
          localStorage.removeItem('setIndex');
          localStorage.removeItem('sgNavSearchList');
          this.PopMenuHideClick();
        },
        _isMobile() {
          let flag = navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i)
          return flag;
        }
        }
    }


</script>




